<template>
<div class="md-layout service-card">
  <div class="md-layout-item md-size-10 bd-xsmall-hide"></div>
  <div class="md-layout-item md-size-30 md-xsmall-size-100 photo-frame">
    <img class="service-photo" :src="object.photo">
    <uploadFile class="photo-uploader" ref="photoUploader" :manager="manager" acceptType="image" :subFolder="manager.user._id + '/resources'" :next="uploaded" v-if="edit"/>
  </div>
  <div class="md-layout-item md-size-10 bd-xsmall-hide"></div>
  <div class="md-layout-item md-size-40 md-xsmall-size-100">
    <p class="md-display-1">{{object.title}}</p>
    <p class="md-body-1">{{object.description}}</p>
  </div>

  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary add-service" @click="addService()" v-if="!edit">
    <md-icon>add</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary delete-service" @click="deleteService(index)" v-if="edit">
    <md-icon>close</md-icon>
  </md-button>
  <md-button md-theme="default" class="md-fab md-mini md-plain md-primary edit-service" @click="editService = true" v-if="edit">
    <md-icon>edit</md-icon>
  </md-button>
  <md-dialog-prompt
    md-theme="default"
    :md-active.sync="editService"
    v-model="object.description"
    md-title="サービス内容を入力してください。"
    md-input-maxlength="100"
    md-input-placeholder="Type..."
    md-confirm-text="確定"
    md-cancel-text="キャンセル"
  />
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import uploadFile from '@/components/common/uploadFile'
export default {
  props: {
    manager: {
      default: 'manager'
    },
    object: {
      default: 'object'
    },
    edit: {
      type: Boolean,
      default: true
    },
    index: {
      default: null
    }
  },
  components: {
    uploadFile
  },
  data: () => ({
    editService: false
  }),
  methods: {
    addService () {
      if (!this.manager.homepageService.homepage.services) this.manager.homepageService.homepage.services = []
      this.manager.homepageService.homepage.services.push(this.object)
    },
    deleteService (index) {
      this.manager.homepageService.homepage.services.splice(index, 1)
    },
    uploaded (photo) {
      this.object.photo = photo.file
    }
  }
}
</script>

<style scoped lang="scss">
.service-card {
  margin: 80px 0;
  position: relative;
}
.photo-frame {
  position: relative;
}
.service-photo {
  width: 100%;
  height: 240px;
  object-fit: cover;
}
.photo-uploader {
  position: absolute;
  bottom: 6px;
  right: 0px;
}
.delete-service {
  position: absolute;
  top: 0;
  right: 16px;
}
.edit-service, .add-service {
  position: absolute;
  bottom: 0;
  right: 16px;
}
</style>



