<template>
<div ref="animate" class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title">トピックス</p>
    <md-button class="md-accent hp-more" @click="manager.homepageService.switchPage('topics')">
      more
    </md-button>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="component-wrapper">
    <infoLable :manager="manager" :object="info" v-for="(info, index) in manager.homepageService.dummyInfos" :key="info._id" v-if="index < 3"/>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import infoLable from '../shared/infoLable'
export default {
  props: ['manager', 'config'],
  components: {
    infoLable
  }
}
</script>

<style scoped lang="scss">

</style>



