<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title">トピックス</p>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <md-content class="info-center md-scrollbar">
    <infoCard :manager="manager" :object="info" v-for="info in manager.homepageService.dummyInfos" :key="info._id"/>
  </md-content>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import infoCard from '../shared/infoCard'
export default {
  props: ['manager', 'config'],
  components: {
    infoCard
  }
}
</script>

<style scoped lang="scss">
.info-center {
  height: 350px;
  overflow: auto;
  width: 100%;
  padding: 5px;
}
.info-card + .info-card {
  margin-top: 10px;
}
</style>



