<template>
<div class="component-wrapper">
  <div class="hp-info-title">
    <md-content class="hp-title-bar md-primary" v-if="manager.homepageService.page.design === '1'"></md-content><p class="md-title">お役立ち資料</p>
    <uploadFile class="file-uploader" ref="fileUploader" :manager="manager" :subFolder="manager.user._id + '/resources'" :next="uploaded" v-if="!manager.homepageService.homepage.downloads || manager.homepageService.homepage.downloads.length < 3"/>
    <md-content class="hp-title-line md-primary" v-if="manager.homepageService.page.design === '2'"></md-content>
  </div>
  <div class="md-layout md-alignment-top-center" v-if="manager.homepageService.homepage.downloads && manager.homepageService.homepage.downloads.length > 0">
    <div class="md-layout-item md-size-30 md-medium-size-33 md-small-size-50 md-xsmall-size-100" v-for="(file, index) in manager.homepageService.homepage.downloads" :key="index">
      <fileCard class="animate" :manager="manager" :file="file" :index="index"/>
    </div>
  </div>
</div>
</template>

<script>
// import utils from '@/tool/utils.js'
import manager from '@/store/manager.js'
import fileCard from '../shared/fileCard'
import uploadFile from '@/components/common/uploadFile'
export default {
  props: ['manager', 'config'],
  components: {
    fileCard,
    uploadFile
  },
  methods: {
    uploaded (file) {
      if (!manager.homepageService.homepage.downloads) manager.homepageService.homepage.downloads = []
      file.title = file.name.split('.')[0]
      manager.homepageService.homepage.downloads.push(file)
    }
  }
}
</script>

<style scoped lang="scss">
.file-uploader {
  position: absolute;
  top: 12px;
  right: 10px;
}
.md-layout {
  margin: 40px 0;
}
.md-layout-item {
  margin-bottom: 20px;
}
</style>



