<template>
<div>
  <vue-headful title="ブドウさん - 本人"/>
  <div class="md-layout md-gutter md-alignment-top">
    <div class="md-layout-item md-size-30 md-medium-size-40 md-small-size-100">
      <div class="row">
        <contactCard :manager="manager" :object="manager.user"/>
      </div>
      <div class="row">
        <md-button class="md-accent md-raised action-button" to="/my/profile">
          <div class="icon-text">
            <md-icon>edit</md-icon><p>編集</p>
          </div>
        </md-button>
        <md-button class="md-accent md-raised action-button" @click="signout()">
          <div class="icon-text">
            <md-icon>power_settings_new</md-icon><p>サインアウト</p>
          </div>
        </md-button>
      </div>
    </div>
    <div class="md-layout-item md-size-70 md-medium-size-60 md-small-size-100">
      <p class="md-title">ホームページ工房</p>
      <md-divider></md-divider>
      <p class="md-body-2">工房の鉄則</p>
      <p class="md-body-1" style="margin-top: 24px;">その1. サンプルさを極める</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>マニュアル要らず</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>一番重要な物は一番便利にアクセス</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>文字入力は必要最小限</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>機能ツリーの深さは最小、そして選択は最少回数</p>
      <p class="md-body-1" style="margin-top: 24px;">その2. あなたが見たもの、それがあなたの得るもの</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>ビジュアルな結果を見せること</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>リアルタイムに見せること</p>
      <p class="md-body-1 indent"><md-icon class="md-primary">keyboard_arrow_right</md-icon>その場で編集すること</p>
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/homepage">
          <div class="icon-text">
            <md-icon>settings</md-icon><p>工房に入る</p>
          </div>
        </md-button>
      </div>
      <p class="md-title">トピックスセンター</p>
      <md-divider></md-divider>
      <p class="md-body-1">ホームページのトピックスへ情報を発信する。</p>
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/topics">
          <div class="icon-text">
            <md-icon>new_releases</md-icon><p>トピックス管理</p>
          </div>
        </md-button>
      </div>
      <p class="md-title">お問い合せ履歴</p>
      <md-divider></md-divider>
      <p class="md-body-1">お客様がホームページより送信したお問い合せを確認する。</p>
      <div class="actions">
        <md-button class="md-accent md-raised action-button" to="/my/messages">
          <div class="icon-text">
            <md-icon>forum</md-icon><p>お問い合せ一覧</p>
          </div>
        </md-button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import utils from '@/tool/utils.js'
// import manager from '@/store/manager.js'
import contactCard from '@/components/shared/contactCard'
export default {
  props: ['manager'],
  components: {
    contactCard
  },
  methods: {
    signout () {
      utils.restGet('/logout').then(
        response => {
          if (response) {
            location.href = location.protocol + '//' + location.host + '/'
          }
        }
      )
    }
  }
}
</script>

<style scoped lang="scss">
.row {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.action-button {
  width: 150px;
}
.actions {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.indent {
  padding-left: 16px;
  display: flex;
  align-items: center;
  .md-icon {
    margin: 0;
  }
}
</style>



